.Index {
  background: #001220;
  background-image: url(../src/Images/index-background.svg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 100vh;
}
.Index .content {
  top: 30%;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  justify-content: center;
  max-width: 100vw;
}
.Index .content h1 {
  font-family: "Aquire";
  color: #FFFFFF;
  margin: 0;
  font-size: 2.4em;
}
.Index .content h1 span {
  display: block;
}
.Index .content .portfolio-text {
  color: #ffffff;
  margin: 0;
  font-size: 2.4em;
  height: 10vh;
  width: 13ch;
  animation: typing 2.3s steps(13), blink 0.5s step-end infinite alternate;
  white-space: nowrap;
  overflow: hidden;
  border-right: 3px solid;
  font-family: monospace;
  font-size: 6em;
}
@keyframes typing {
  from {
    width: 0;
  }
}
@keyframes blink {
  50% {
    border-color: transparent;
  }
}