.members {
  color: white;
  margin-bottom: 25px;
    background-image: url(../src/Images/Members-background.svg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    overflow-y: auto;
    height: 100%;
}

.members .member{
    margin: 20px;
    padding: 20px;
    border-radius: 10px;
    background-color: #001220;
    height: 300px;
    align-items: center;
}
.members .member .photos .Description{
  flex: 1; /* Take up remaining space */
  text-align: center;
  font-size: 15px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis; /* Add this if you want to show ellipsis for long text */
}
.members .member .photos {
  margin: 0 auto;
  transform: translateY(-20%);
  display: flex;
  flex-direction: row;
  padding-right: 20px;
  border-radius: 10px;
  }
  
  .members .member .photos img {
    margin-right: 25px;
    height: 250px;
    width: auto;
    order: 1;
    }
  
    .members .member .photos .gif {
      margin-left: 0px;
      margin-top: auto;
      height: 120px;
      width: auto;
      order: -1;
      transform: translateY(0%);
    }
    
    .members .member .Favorite {
      transform: translateY(-230%);
      color: rgb(255, 255, 255);
      font-size: 20px;
    }
    
    .members .member .photos img:last-child,
    .members .member .photos .gif:last-child {
      margin-right: 0;
    }
    
    .members .member .photos::before,
    .members .member .photos::after {
      content: '';
      flex: 1;
    }